<template lang="pug">
	div.container
		h1 Параметры меню 

		table.table.table-sm
			tbody 
				tr
					td 
						label.mr-3 Уникальное название меню: 
					td 
						input.form-control(type="text", placeholder="Только англ. буквы", size="32", v-model="name")
				tr
					td 
						label.mr-3 Команда открытия меню: 
					td 
						input.form-control(type="text", placeholder="Например, sm_mymenu", size="32", v-model="command")
				tr
					td 
						label.mr-3.form-check-label Тип: 
					td 
						input#type-menu.ml-3(name="menu-type", type="radio", value="menu", v-model="type", @change="sort_items") 
						label.mr-3.ml-2.form-check-label(for="type-menu") Меню 
						input#type-panel(name="menu-type", type="radio", value="panel", v-model="type", @change="sort_items") 
						label.mr-3.ml-2.form-check-label(for="type-panel") Панель 
				tr 
					td 
						label.mr-3 "Назад" возвращает в меню: 
					td 
						input.form-control(type="text", placeholder="Опционально, название меню", size="32", v-model="exit_back")

		h1 Содержимое меню 
		div.row
			div.col-xl-7
				h2.text-center Меню 
				div.row.mb-4
					div.input-group
						div.input-group-prepend
							span.input-group-text Заголовок 
						input#menu-title.form-control(type="text", placeholder="Введите заголовок меню", v-model="title")
				
				div.mb-3(v-for="item of items")
					div.row
						div.input-group
							div.input-group-prepend
								span(v-if="type==='menu'").input-group-text {{ item.position }}
								select(v-else, v-model:value="item.position", @change="sort_items").input-group-text
									option(value=" ") 
									option(v-for="value in 9" :value="value") {{ value }}
									option(value="0") 0
							input.form-control(type="text", placeholder="Отображаемый текст", v-model="item.text")
							div.input-group-append
								button.btn.btn-secondary(@click="delete_item(item)") X

					div.row
						div.input-group(v-for="action of item.actions")
							div.input-group-prepend
								span.input-group-text(v-if="action.action === 'open'") Открыть меню 
								span.input-group-text(v-if="action.action === 'pcmd'") Выполнить от игрока
								span.input-group-text(v-if="action.action === 'scmd'") Выполнить от сервера
								span.input-group-text(v-if="action.action === 'close'") Закрыть меню

							input.form-control(v-if="action.action === 'open'", type="text", placeholder="Название меню", v-model="action.target")
							input.form-control(v-if="action.action === 'pcmd'", type="text", placeholder="Команда от лица игрока", v-model="action.target")
							input.form-control(v-if="action.action === 'scmd'", type="text", placeholder="Команда от сервера", v-model="action.target")
							input.form-control(v-if="action.action === 'close'", type="text", placeholder="", v-model="action.target", disabled)

							div.input-group-append
								button.btn.btn-secondary(@click="delete_action(item, action)") X

						div.mt-1.mx-auto
							button.btn.btn-outline-secondary.btn-sm(type="button", data-toggle="dropdown", aria-haspopup="true", aria-expanded="false")  Добавить действие 
							div.dropdown-menu
								button.dropdown-item(@click="add_action(item, 'open')") Открыть меню 
								button.dropdown-item(@click="add_action(item, 'pcmd')") Выполнить команду от игрока 
								button.dropdown-item(@click="add_action(item, 'scmd')") Выполнить команду от сервера 
								button.dropdown-item(@click="add_action(item, 'close')") Закрыть меню 
				
				div.row.mb-3
					div.mx-auto
						button.btn.btn-outline-secondary.mt-3(@click="add_item") Добавить пункт 
			
			div.col-xl

			div.col-xl-4
				h2.text-center Предпросмотр 
				div.menu-preview.mb-4.shadow-lg(v-for="i in Math.ceil(items.length / items_per_page)")
					div.overflow-hidden
						div.item-muted.raw-html {{ process_text(title) }}

					div.overflow-hidden(v-for="item in items.slice((i - 1) * items_per_page, i * items_per_page)")
						div.raw-html(v-if="type==='menu'" v-bind:class="{'item-muted': item.actions.length === 0}") {{ ((item.position - 1) % items_per_page) + 1 }}. {{ process_text(item.text) }}
						div.raw-html(v-else)
							span(v-if="item.position !== ' '" v-bind:class="{'item-muted': item.actions.length === 0}") {{ item.position }}. 
							span(v-bind:class="{'item-muted': (item.actions.length === 0 || item.position === ' ')}") {{ process_text(item.text) }}
					
					div(v-if="type === 'menu'")
						div(v-if="i > 1 || exit_back !== ''")
							div &nbsp;
							div.item-muted {{ items_per_page + 1}}. Назад
							div(v-if="items.length / items_per_page > i")
								div.item-muted {{ items_per_page + 2}}. Вперёд
						div(v-else)
							div &nbsp;
							div(v-if="items.length / items_per_page > i")
								div.item-muted {{ items_per_page + 2}}. Вперёд

						div.item-muted {{ (items_per_page === 6) ? 9 : 0}}. Выход
		
		div.d-xl-flex.justify-content-end.text-center
			div.my-auto.d-inline
				button.btn.btn-sm.btn-danger.mr-3(@click="delete_menu") Удалить меню
			button.btn.btn-lg.btn-warning.mr-3(@click="go_back") Назад
			button.btn.btn-lg.btn-primary(@click="save_menu") Сохранить меню 
</template>

<style>
.menu-preview {
	border-radius: 10px;
	padding: 10px 20px;
	background-color: #666;
	color: #dda627;
	font-size: 18pt;
}

.item-muted {
	color: #e6e1c3;
}

.overflow-hidden {
	overflow: hidden;
}

.raw-html {
	white-space: pre-wrap;
}
</style>

<script>
export default {
	name: "edit",

	data: function() {
		return {
			// name: "main",
			// title: "Добро пожаловать\\nна супер сервер",
			// items: [
			// 	{
			// 		position: 1,
			// 		text: "Полезные команды",
			// 		actions: [{ action: "open", target: "commands" }]
			// 	},
			// 	{
			// 		position: 2,
			// 		text: "Правила сервера",
			// 		actions: [{ action: "open", target: "rules" }]
			// 	},
			// 	{
			// 		position: 3,
			// 		text: "О сервере",
			// 		actions: [{ action: "open", target: "about" }]
			// 	},
			// 	{
			// 		position: 4,
			// 		text: "Контакты",
			// 		actions: [{ action: "open", target: "contacts" }]
			// 	}
			// ],

			name: "",
			base_name: "",
			title: "",
			items: [],
			items_per_page: 7,
			type: "menu",
			exit_back: "",
			command: ""
		};
	},

	created: function() {
		$(function() {
			$('[data-toggle="tooltip"]').tooltip();
		});

		this.base_name = this.name = localStorage.getItem("name");
		if (this.name.length > 0) {
			let proxy = JSON.parse(
				localStorage.getItem("_menubuilder_" + this.name)
			);
			this.title = proxy.title;
			this.items = proxy.items;
			this.type = proxy.type;
			this.command = proxy.command;
			this.exit_back = proxy.exit_back;
		}

		let game = localStorage.getItem("game") || "csgo";
		this.items_per_page = game === "csgo" ? 6 : 7;
	},

	methods: {
		add_item: function() {
			this.items.push({
				position: this.items.length + 1,
				text: "",
				actions: []
			});
		},

		delete_item: function(item) {
			let item_index = this.items.indexOf(item);
			this.items.splice(item_index, 1);

			for (item_index in this.items) {
				this.items[item_index].position = Number(item_index) + 1;
			}
		},

		add_action: function(item, action) {
			let item_index = this.items.indexOf(item);
			this.items[item_index].actions.push({ action, target: "" });
		},

		delete_action: function(item, action) {
			let item_index = this.items.indexOf(item);
			let action_index = this.items[item_index].actions.indexOf(action);
			this.items[item_index].actions.splice(action_index, 1);
		},

		process_text: function(text) {
			return text.replace(/\\n/g, "\n");
		},

		save_menu: function() {
			if (this.base_name !== this.name && this.base_name.length > 0) {
				localStorage.removeItem("_menubuilder_" + this.base_name);
			}
			localStorage.setItem(
				"_menubuilder_" + this.name,
				JSON.stringify({
					title: this.title,
					items: this.items,
					type: this.type,
					command: this.command,
					exit_back: this.exit_back
				})
			);
			this.$router.push("/");

			// if (typeof window.localStorage !== "undefined") {
			//
			// } else {
			// 	let menus = this.$cookies.get("menus");
			// 	if (menus === null) {
			// 		menus = {};
			// 	}

			// 	menus[this.name] = proxy;
			// 	this.$cookies.set("menus", JSON.stringify(menus));
			// }
		},

		delete_menu: function() {
			this.$swal({
				title: "ВНИМАНИЕ",
				text: "Подтвердите удаление меню",
				icon: "warning",
				buttons: ["Отмена", "Удалить"],
				dangerMode: true
			}).then(value => {
				if (value === true) {
					localStorage.removeItem("_menubuilder_" + this.base_name);
					this.$router.push("/");
				}
			});
		},

		sort_items: function() {
			if (this.type === "panel") {
				let position_index;
				let position = 0;
				for (let i = 0; i < this.items.length; ++i) {
					if (this.items[i].position !== " ") {
						if (this.items[i].position == 0) {
							this.items[i].position = 100;
						}
						if (this.items[i].position > position) {
							position = this.items[i].position;
							position_index = i;
						} else if (this.items[i].position === position) {
							this.items[i].position++;
							position = this.items[i].position;
							position_index = i;
						} else {
							position++;
							this.items[i].position = position;
							position_index = i;
						}
						if (this.items[i].position == 100) {
							this.items[i].position = 0;
						}
					}
				}
			} else {
				for (let i = 0; i < this.items.length; ++i) {
					this.items[i].position = i + 1;
				}
			}
		},

		go_back: function() {
			let old = localStorage.getItem("_menubuilder_" + this.name);
			if (old) {
				let newobj = JSON.stringify({
					title: this.title,
					items: this.items,
					type: this.type,
					command: this.command,
					exit_back: this.exit_back
				});

				if (old !== newobj) {
					this.$swal({
						title: "ВНИМАНИЕ",
						text: "Вы не сохранили изменения",
						icon: "warning",
						buttons: ["Остаться", "Не сохранять"],
						dangerMode: true
					}).then(value => {
						if (value === true) {
							this.$router.push("/");
						}
					});
				} else {
					this.$router.push("/");
				}
			} else {
				this.$router.push("/");
			}
		}
	},

	beforeRouteLeave: function(to, from, next) {
		next();
	}
};
</script>
