<template lang="pug">
	div.container
		h1 Настройки 
		table.table.table-sm.text-center
			thead
				tr 
					th Параметр
					th Значение
			tbody 
				tr 
					td Игра
					td 
						input#game-csgo.ml-3(name="menu-type", type="radio", value="csgo", v-model="game") 
						label.mr-3.ml-2.form-check-label(for="game-csgo") CS:GO 
						input#game-css(name="menu-type", type="radio", value="css", v-model="game") 
						label.mr-3.ml-2.form-check-label(for="game-css") CS:S 

		h1 Список меню 
		table.table.shadow
			thead.thead-light
				tr.text-center
					th #
					th Название
					th Действия
			tbody 
				tr.text-center(v-for="menu, index in menus")
					th(scope="row") {{ index + 1 }}
					td {{ menu }}
					td 
						button(@click="delete_menu(menu)").btn.btn-sm.btn-danger.mr-3 Удалить 
						button(@click="edit_menu(menu)").btn.btn-sm.btn-primary Редактировать 

		div.d-flex.justify-content-between
			button(@click="$router.push('/generate')").btn.btn-lg.btn-info.mx-auto.mt-3 Сгенерировать плагин
			button(@click="create_menu").btn.btn-lg.btn-primary.mx-auto.mt-3 Добавить новое меню
</template>

<script>
export default {
	name: "index",

	data: function() {
		return {
			game: "csgo",
			menus: []
		};
	},

	created: function() {
		let key;
		let prefix = "_menubuilder_";
		for (let i = 0; i < localStorage.length; ++i) {
			key = localStorage.key(i);
			if (key.startsWith(prefix)) {
				this.menus.push(key.substr(prefix.length));
			}
		}

		this.game = localStorage.getItem("game") || "csgo";
	},

	methods: {
		create_menu: function() {
			localStorage.setItem("name", "");
			this.$router.push("edit");
		},

		edit_menu: function(menu) {
			localStorage.setItem("name", menu);
			this.$router.push("edit");
		},

		delete_menu: function(menu) {
			this.$swal({
				title: "ВНИМАНИЕ",
				text: "Подтвердите удаление меню",
				icon: "warning",
				buttons: ["Отмена", "Удалить"],
				dangerMode: true
			}).then(value => {
				if (value === true) {
					localStorage.removeItem("_menubuilder_" + menu);
					this.menus.splice(this.menus.indexOf(menu), 1);
				}
			});
		}
	},

	beforeRouteLeave: function(to, from, next) {
		localStorage.setItem("game", this.game);
		next();
	}
};
</script>

